import { GET_DOGS, REFRESH_DOGS, DETAIL_DOG } from "./actions"


const initialState = {
    dogs: [],
    details: {},
}

    const reducer = (state = initialState, action) => {
    switch (action.type) {
        case GET_DOGS:
            return {
                ...state, 
                dogs: action.payload
            };

            case DETAIL_DOG:
                return {
                    ...state,
                    details: action.payload
                }

        case REFRESH_DOGS:
            return {
                ...state,
                details: {}
            }  
        default:
            return { ...state };
    }
}

export default reducer