import React from 'react'
import { useSelector } from 'react-redux'
import style from '../Card/Card.module.css'

function Card() {


    const dogs = useSelector((state) => state.dogs)

    

  return (
    <div  className={style.card}  >
          {
          dogs.map((d, i) => {
            const weight = d.weight.map((w) => w)
            return (
              <div className={style.single_dog} key={d.id}>
                    <h2>{d.name}</h2>
                    <div className={style.weight}>
                    Weight:<ul>
                    <li>Min: {weight[0]}kg</li>
                    <li>Max: {weight[1]}kg</li>
                    </ul>
                    </div>
                    
                    <div>
                    <img src={d.image} alt={`imagen de: ${d.name}`} className={style.img} width='350px' height='250px' />
                    </div> 
                    <p>Temperaments:</p>
                    {d.temperaments.map((temps) => <h3 key={temps+Math.random}>{temps}</h3>)}
              </div>
              )
            })
          }
    </div>
  )
}

export default Card
