import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getDogs } from '../../redux/actions'
import Card from '../Card/Card'
import style from '../Home/Home.module.css'

function Home() {

  const dispatch = useDispatch()

  const dogs = useSelector((state) => state.dogs)

  useEffect(() =>{
    dispatch(getDogs())
  },[dispatch])


  return (
    <div className={style.all}>
        <h1 className={style.home}>Soy el Home</h1>
        <input type='text' name='dogName' className={style.searchBar}></input>
        <select>
          <option>Orden alfabetico</option>
          <option>Peso</option>
        </select>
        <select>
          <option>Ascendente</option>
          <option>Descendente</option>
        </select>

        <select>
          <option>Raza</option>
          <option>Temperamento</option>
        </select>
        <div className={style.card}>
        {dogs.map(d => <Card key={d.id} name={d.name} weight={d.weight} temperament={d.temperament} />)}
        </div>
    </div>
  )
}

export default Home
